import React from 'react';
import { Layout, Menu, Typography } from 'antd';
import { HomeOutlined, AppstoreOutlined, BulbOutlined, UserOutlined, PhoneOutlined } from '@ant-design/icons';
import { Link } from 'react-scroll';

const { Header } = Layout;
const { Text } = Typography;

const CustomHeader = () => {
  const scrollToSection = (id) => {
    const element = document.getElementById(id);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
  };

  return (
    <Header>
      <div className="logo" />
      <Menu theme="dark" mode="horizontal" defaultSelectedKeys={['home']} style={{ lineHeight: '64px' }}>
        <Menu.Item key="home" icon={<HomeOutlined />}>
          <Link to="home" spy={true} smooth={true} offset={-70} duration={500}>
            MGTECH
          </Link>
        </Menu.Item>
        <Menu.Item key="services" icon={<AppstoreOutlined />}>
          <Link to="services" spy={true} smooth={true} offset={-70} duration={500}>
            Serviços
          </Link>
        </Menu.Item>
        <Menu.Item key="about" icon={<BulbOutlined />}>
          <Link to="about" spy={true} smooth={true} offset={-70} duration={500}>
            Sobre
          </Link>
        </Menu.Item>
        <Menu.Item key="contact" icon={<PhoneOutlined />}>
          <Link to="contact" spy={true} smooth={true} offset={-70} duration={500}>
            Contato
          </Link>
        </Menu.Item>
        <Menu.Item key="login" icon={<UserOutlined />} style={{ marginLeft: 'auto', float: 'right' }}>
          <a href="#login">Login</a>
        </Menu.Item>
      </Menu>
    </Header>
  );
};

export default CustomHeader;
